
import axios from 'axios'
import Toast from 'vux/src/components/toast/index.vue'

import jquery from "components/mine/jquery-2.2.4.min.js"
export default {
data() {
const userName = window.localStorage.getItem("userName")
return {
brandTitle: '',
brandTxt: '',
userName,
aliKey: false,
show: false,
AlipayAccount: ''
}
},
methods: {
grap() {
var self = this
var url = "http://121.42.177.97:8080/red/payred/getmoney.do";
var user_name = this.userName;
console.log(user_name);
var prize_msg = this.brandTitle;
var coupon_name = this.brandTxt;
var params = {
"user_name": user_name,
"prize_msg": prize_msg,
"coupon_name":prize_msg
};
debugger
$.post(url, params, function(result) {
console.log(result)
if(result.state == 1) {
console.log(result.data);
var getmonkey = result.data.getmonkey;
var prize_msg = result.data.prize_msg;
window.localStorage.setItem('prize_msg', result.data.prize_msg)
window.localStorage.setItem('getmonkey', result.data.getmonkey)
console.log(prize_msg);
self.$vux.toast.show({
text: '成功抢到红包'
})
self.$router.push({
path: "/message/grap/record"
})



} else {
self.$vux.toast.show({
type: 'cancel',
text: result.message
})





}
})
}
}
}



